import React from 'react'
import App, { Container } from 'next/app'
import { Provider } from 'react-redux'
import { createStore } from 'redux'

import { validatePath } from '../routes'
import {
  SignIn,
  Navbar,
  Sidebar,
  Head,
} from '../feature'
import reducers from '../reducer'
import styles from '../theme/_app.scss';
import NotFound from './not-found';
import { initialize } from '../firebase'

const store = createStore(reducers)

export default class MyApp extends App {
  static async getInitialProps({ Component, router, ctx }) {
    let pageProps = {}
    if (Component.getInitialProps) {
      pageProps = await Component.getInitialProps(ctx)
    }
    return { pageProps, router }
  }

  constructor(props) {
    super(props)
    initialize()
  }

  state = {
    isNotFoundRoute: false,
    isSignedIn: true,
  }

  _onSignedIn = (isSignedIn) => {
    this.setState({ isSignedIn })
  }

  renderApp = () => {
    const {
      Component,
      pageProps,
      router
    } = this.props
    const { isSignedIn } = this.state
    const { isNotFoundRoute } = this.state
    const isCorrectRoute = router.asPath === '/' || validatePath(router.asPath)
    if (!isCorrectRoute) {
      return <NotFound />
    }
    if (isCorrectRoute && !isSignedIn) {
      return <SignIn isSignedIn={this._onSignedIn} />
    }
    return (
      <React.Fragment>
        <Head />
        {!isNotFoundRoute && (
          <React.Fragment>
            <Sidebar />
            <Navbar />
          </React.Fragment>
        )}
        <div className={styles.contentLayout}>
          <Component {...pageProps} />
        </div>
      </React.Fragment>
    )
  }

  render() {
    return (
      <Container>
        <Provider store={store}>
          {this.renderApp()}
        </Provider>
      </Container >
    )
  }
}